.data {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.data__link {
  display: block;
  width: 100%;
  height: 100%;
  transition: all .3s;
}

.data__link--contact {
  &:hover {
    color: rgb(44, 0, 84);
    transform: scale(1.08);
  }
}

.data__caption {
  text-align: center;
  font-family: "Roboto-Lite", sans-serif;
  font-size: 30px;
  line-height: 150%;
  font-weight: normal;
  border-bottom: 1px solid #b4c4e2;
  margin-top: 50px;
}

.data__subcaption {
  font-size: 20px;
  line-height: 150%;
  font-weight: normal;
  margin-top: 0;
}

.data__year {
  text-transform: uppercase;
  font-weight: 900;
}

.data__institution {
  font-weight: bold;
  font-style: italic;
}

.data__list {
  width: 100%;
  list-style: none;

  &--info {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &--skill {
    list-style: circle;
    display: flex;
    flex-wrap: wrap;
    max-height: 200px;
    padding-left: 30px;
    align-items: center;
    flex-direction: column;
    font-size: 18px;

    @media (max-width: 560px) {
      list-style: none;
      max-height: none;
      padding-left: 0;
      flex-direction: row;
      align-items: center;
    }

    @media (max-width: 414px) {
      justify-content: space-around;
    }
  }

  &--ed {
    @media (max-width: 560px) {
      list-style: circle;
      text-align: left;
      padding-left: 20px;
    }
  }

  &--portfolio {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 350px));

    justify-content: center;
    grid-column-gap: 30px;
    grid-row-gap: 40px;
  }

  &--real {
    margin-bottom: 50px;
    padding-bottom: 10px;
  }

  &--work {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;
    @media (max-width: 560px) {
      margin-bottom: -20px;
    }
  }
}

.data__item--work {
  width: 30%;
  background: linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(219, 228, 245, 1) 100%, rgba(219, 228, 245, 1) 100%);
  border: 1px solid rgb(192, 188, 188);
  border-radius: 5px;
  padding: 10px 5px 10px 5px;

  @media (max-width: 560px) {
    width: 48%;
    margin-bottom: 20px;
  }

  @media (max-width: 460px) {
    width:100%;
    margin-bottom: 20px;
  }
}

.data__item--info {
  &:first-child {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
  }
  &:last-child {
    width: 70%;
    margin: 0 auto;
  }

  width: 49%;
  background: linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(219, 228, 245, 1) 100%, rgba(219, 228, 245, 1) 100%);
  border: 1px solid rgb(192, 188, 188);
  border-radius: 5px;
  padding: 10px 5px 10px 5px;
  margin-bottom: 10px;

  @media (max-width: 500px) {
    width: 100%;
  }
}

.data__item--skill {
  @media (max-width: 560px) {
    width: 48%;
    margin-right: 0px;
    background: linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(219, 228, 245, 1) 100%, rgba(219, 228, 245, 1) 100%);
    border: 1px solid rgb(192, 188, 188);
    border-radius: 5px;
    padding: 10px 15px 10px 15px;
    margin-bottom: 10px;
    text-align: center;

    &:nth-child(odd) {
      margin-right: 15px;
    }
  }

  @media (max-width: 414px) {
    width: 47%;
  }

  width: none;
  text-align: start;
  border-bottom: 1px solid #cad0dd;
}

.data__item--skill:not(:last-child) {
  margin-bottom: 10px;
}

.data__item--portfolio {
  position: relative;
  transition: 0.5s;
  width: 100%;
  height: 200px;
  border-radius: 0.5rem;

  &:hover {
    transform: scale(1.05);
    transition: 0.5s;
    box-shadow: 0px 0px 20px 1px #61769c;
  }
}

.data__item--portfolio img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
  border-radius: 0.5rem;
}

.data__textarea {
  width: 100%;
  height: 200px;
  padding: 20px;
  resize: none;
  margin-bottom: 30px;
  font-size: 18px;
  line-height: 150%;
  background: linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(219, 228, 245, 1) 100%, rgba(219, 228, 245, 1) 100%);
  border-radius: 0.5rem;

  @media (max-width: 560px) {
    font-size: 16px;
    padding: 10px;
  }
}

.data__wrapper {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
}


.page__row {
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  // margin: 50px 0;
  font-size: 50px;
  line-height: 100%;
  font-weight: bolder;
}

.page__counter {
  display: inline-block;
  width: 70px;
  padding: 5px;
  border-radius: 5px;
  background: linear-gradient(90deg,#fff 0,#dbe4f5 100%,#dbe4f5 100%);

  & span {
    font-size: 70px;
  }
}

.modal {
  max-width: 700px;
}

.modal__remarck {
  padding: 20px;
  font-size: 20px;
}

.remarck span {
  display: inline-block;
  transition: all 1s;
}


.remarck:hover span {
  color: red;
  transform: rotateY(360deg);
}

.deta__team-site {
  transition: all 0.5s;
  &:hover {
    font-weight: bold;
  }
}

.hero {
  position: relative;
  display: flex;
  padding-right: 100px;
}

.hero__content {
  width: 60%;
  margin-right: 44px;
  align-self: center;
}

.hero__img {
  flex-shrink: 0;
  border-radius: 40px;

  & img {
    flex-shrink: 0;
  }
}

.hero__buttons {
  display: flex;
  position: relative;
}

.hero__buttons-sticker {
  position: absolute;
  top: 0;
  left: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px 8px;

  width: 144px;
  height: 32px;

  background: #FF6700;
  border-radius: 8px;
  transform: rotate(-15deg);

  font-family: 'Roboto Flex';
  font-style: normal;
  font-weight: 750;
  font-size: 14px;
  line-height: 20px;
  color: #FFFFFF;
}

.hero__caption {
  text-align: left;
  font-family: 'Roboto Flex';
  font-style: normal;
  font-weight: 750;
  font-size: 48px;
  line-height: 48px;
  /* or 100% */

  letter-spacing: -0.5px;

  /* Черный */

  color: #090909;
  font-stretch: 151;
  font-variation-settings: 'GRAD' 0, 'slnt' 0, 'XTRA' 499, 'XOPQ' 96, 'YOPQ' 79, 'YTLC' 514, 'YTUC' 712, 'YTAS' 750, 'YTDE' -203, 'YTFI' 738;
}

.hero__link {
  display: inline-block;
  width: 200px;
  background: #6A6A6A;
  border-radius: 36px;
  color: white;
  padding: 12px 0;
  padding-left: 40px;

  &--app {
    background-image: url("../img/svg/apple.svg");
    background-position: center left 17px;
    background-repeat: no-repeat;
  }

  &--play {
    background-image: url("../img/svg/play.svg");
    background-position: center left 17px;
    background-repeat: no-repeat;
  }

  &:first-child {
    margin-right: 24px;
  }

  & span {
    display: inline-block;
    font-weight: bold;
  }

}

.next {
  display: flex;
  margin: 0 auto;
  margin-top: 42px;
  justify-content: center;
}

.next__page {
  display: block;

  font-family: 'Roboto Flex';
  font-style: normal;
  font-weight: 550;
  font-size: 14px;
  line-height: 20px;
  /* identical to box height, or 143% */

  letter-spacing: 0.25px;

  /* Черный */

  color: #090909;
  font-stretch: 151;
  font-variation-settings: 'GRAD' 0, 'slnt' 0, 'XTRA' 499, 'XOPQ' 96, 'YOPQ' 79, 'YTLC' 514, 'YTUC' 712, 'YTAS' 750, 'YTDE' -203, 'YTFI' 738;
}

.paginator {
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 0;
  top: 100px;
}

.paginator__link {
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #6A6A6A;
  cursor: pointer;
  margin-right: 10px;
}

.paginator__link--current {
  transform: translateX(-2px);
  position: relative;
  background-color: white;
  width: 16px;
  height: 16px;
  border: 1px solid #5337FF;
  &::before {
    @include pseudo;
    top:2px;
    left:2px;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    background-color: #5337FF;
  }
}

.paginator__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.paginator__number {
  display: block;
  visibility: hidden;
  font-family: 'Roboto Flex';
  font-style: normal;
  font-weight: 750;
  font-size: 20px;
  line-height: 24px;
  /* identical to box height, or 120% */

  text-align: center;

  /* Синий */

  color: #5337FF;
  font-stretch: 151;
  font-variation-settings: 'GRAD' 0, 'slnt' 0, 'XTRA' 499, 'XOPQ' 96, 'YOPQ' 79, 'YTLC' 514, 'YTUC' 712, 'YTAS' 750, 'YTDE' -203, 'YTFI' 738;
}

.paginator__link--current + .paginator__number {
  visibility: visible;
}

.paginator__item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
